
html,
.container,
.changeorder {
  width: 100%;
}
body {
  margin: 0;
}
.changeorder {
  position: relative;
}
span {
  color: #d0021b;
}
.text {
  display: inline-block;
  font-family: PingFangSC-Regular;
  font-size: 0.28rem;
  color: #333;
  letter-spacing: 0;
  line-height: 0.4rem;
  margin-left: 0.2rem;
}
.sort {
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 0.2rem;
  color: #fff;
  background-color: #bb996c;
  text-align: center;
  line-height: 0.4rem;
  display: inline-block;
}
.sortfirst {
  margin-left: 0.3rem;
  margin-top: 0.6rem;
}
.sortbox {
  margin-left: 0.3rem;
  margin-top: 1.22rem;
}
.line {
  width: 0.04rem;
  height: 3.36rem;
  opacity: 0.3;
  background: #bb996c;
  position: absolute;
  left: 0.48rem;
  top: 0.3rem;
}